export function createAsyncContextLoader<T, U>(options: {
    Provider: React.ComponentType<{ value: T; children?: React.ReactNode }>;
    extract: (u: U) => T;
    fallback?: React.ReactNode;
  }) {
    const { Provider, extract, fallback = null } = options;
  
    function AsyncContextLoaderInner(props: {
      value: Promise<U>;
      children?: React.ReactNode;
    }) {
      console.log('AsyncContextLoaderInner promise:', props.value);
      const raw = use(props.value);
      const contextValue = extract(raw);
      return <Provider value={contextValue}>{props.children}</Provider>;
    }
  
    return function AsyncContextLoader(props: {
      value: Promise<U>;
      children?: React.ReactNode;
    }) {
      return (
        <Suspense fallback={fallback}>
          <AsyncContextLoaderInner {...props} />
        </Suspense>
      );
    };
  }