import { createAsyncContextLoader } from "@fanfanlo";
import { createTypedContext } from "@fanfanlo";
import { ISettingsPageData } from "./settings.data.intf";
import { IStoreWithData } from "@fanfanlo";
import { Fallback } from "@src/components/fallback/Fallback";

export const [SettingsPageDataContextProvider, useSettingsPageData] = createTypedContext<ISettingsPageData>();

export const SettingsPageDataContextLoader = createAsyncContextLoader({
    Provider: SettingsPageDataContextProvider,
    extract: (res: IStoreWithData<ISettingsPageData>) => res.data,
    fallback: <Fallback />,
});