import { IStoreWithData, storeUtils } from "@fanfanlo";
import { settingNs } from "./ns";


export interface ISettingsPageData{

}


function createDefaultSettingsPageData():ISettingsPageData{
    return {
    }
}

function createSettingsPageData():ISettingsPageData{
    return createDefaultSettingsPageData();
}

let loader:Promise<IStoreWithData<ISettingsPageData>>;
export async function loadSettingsPageData():Promise<IStoreWithData<ISettingsPageData>>{
    if(loader)return loader;
    const defData = createSettingsPageData();
    loader = storeUtils.getNamespaceStoreWithData<ISettingsPageData>(settingNs.androidData, {defData, forceReset:false, mergeDefData:true});
    return loader;
}
